body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
  padding:10px;
  font-size: 20px;
  color:white;
  background-color: black;
}
.flex{
	display: flex;
}

.table-header{
	background-color: #484848;
    color: white;
    justify-content: space-around;
    cursor: pointer;
}


.row{
	justify-content: space-around;
	padding: 10px;
	align-items: center;
	font-size: 20px;
}

.row div{
	flex-grow: 1;
	margin-bottom: 10px;
	width:33.33%;
	text-align: center;
}

.table-header div{
	padding: 10px;
	position: relative;
}

.poster{
	width: 100px;
	margin:auto;
	background-color: #d0d0d0;
}


.arrow-up{
    width: 0px;
    border-width: 0px 8px 8px 8px;
    border-style: solid;
    border-color: white transparent white;
    position: absolute;
    top: 8px;
    right: -20px;
}

.arrow-down{
	border-width: 8px 8px 0px 8px;
    border-style: solid;
    border-color: white transparent white;
    position: absolute;
    top: 20px;
    right: -20px;
}
